  content: '';
  height: 2px;
  width: 0;
  bottom: 0px;
  position: absolute;
  background: #f3a491;
  -o-transition: 0.2s ease all;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}
.bar:after {
  right: 50%;
}

.description {
  margin-top: 3rem;
